import {Component, ElementRef} from "@angular/core";
import {PromptComponent} from "./prompt.component";
import {FocusTrapFactory} from "@angular/cdk/a11y";

@Component({
   selector: "vsan-edit-prompt",
   templateUrl: "prompt.html",
   styleUrls: ["prompt.scss"],
})
export class EditPrompt extends PromptComponent {
   constructor(el: ElementRef, focusTrap: FocusTrapFactory) {
      super(el, focusTrap);
      this.confirmText = "vsan.common.apply";
      this.cancelText = "vsan.common.cancel";
   }
}
